export { fetchCountries };
import Notiflix from 'notiflix';

//definicja funkcji, ktra wysya danie HTTP do endpointa (name.official,capital,population,flags,languages) z argumentem name jako nazw kraju, ktr uytkownik wpisuje w formularzu
async function fetchCountries() {
  //pobiera dane z serwera w formacie JSON za pomoc metody fetch()
  return await fetch(
    'https://restcountries.com/v3.1/all?fields=name,capital,population,flags,languages'
  )
    .then(response => {
      if (!response.ok) {
        if (response === 404) {
          throw new Error(404);
        } else {
          throw new Error('No results founded');
        }
      }
      // przekazuje obietnic ktrej wynikiem bdzie tablica krajw bdc wynikiem dania
      // metoda response.json() przetwarza odpowied i zwraca obiekt js jako tablic krajw.
      return response.json();
    })
    .catch(error => {
      Notiflix.Notify.failure('Oops, there is no country with that name');
    });
}
